/* Stiziles */
@import 'fonts/material-icons/material-ui.css';
@import 'fonts/Open_Sans/open_sans.css';
@import 'utils/easing.scss';
@import 'transitions.scss';


html , body {
  box-sizing: border-box;
  font-size: 1.0em;
  font-family: 'Open Sans', 'Helvetica Neue', helvetica, sans-serif;
  font-weight: 100;
  margin: 0;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: subpixel-antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
body.loaded {
  background-color: #fff;
  color: #000;
}


*:focus {
    outline: 0;
}

@import 'pages/app-container.scss';
@import 'pages/play.scss';
@import 'pages/list.scss';
@import 'pages/details.scss';


@import 'components/hexagram-card.scss';
@import 'components/hexagram-image.scss';
@import 'components/tarot_sprites.css';
@import 'responsive.scss';



